@font-face {
  font-family: "Inter";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url(../assets/fonts/Inter/Inter-Regular.ttf) format("truetype");
}
@font-face {
  font-family: "Inter";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../assets/fonts/Inter/Inter-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "Merriweather";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url(../assets/fonts/Merriweather/Merriweather-Regular.ttf) format("truetype");
}
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 20px;
  line-height: 40px;
  font-weight: 400;
  background-color: #000;
  color: #fff;
}
body.lock {
  overflow-y: hidden;
}

h1,
h2,
h3 {
  font-family: "Merriweather", serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s ease;
}
a:hover {
  color: #bdae82;
}
@media (hover: none) {
  a:hover {
    color: inherit;
  }
  a:active {
    color: #bdae82;
  }
}

h1,
h2,
h4,
h3,
ul,
li,
p {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.title-container {
  position: relative;
  width: 50%;
  height: 80px;
  margin: 100px auto 60px;
  text-align: center;
  background-color: #000;
  color: #bdae82;
}
@media (max-width: 768px) {
  .title-container {
    margin: 80px auto 50px;
  }
}
@media (max-width: 550px) {
  .title-container {
    width: 90%;
    margin: 60px auto 30px;
  }
}
.title-container::before {
  position: absolute;
  top: calc(50% - 1px);
  display: block;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #bdae82;
}
.title-container .section-title {
  position: relative;
  width: max-content;
  margin: 0 auto;
  padding: 0 20px;
  font-size: 40px;
  line-height: 80px;
  text-transform: capitalize;
  background-color: #000;
}
@media (max-width: 768px) {
  .title-container .section-title {
    font-size: 32px;
  }
}

.button {
  display: inline-block;
  width: 220px;
  height: 55px;
  line-height: 50px;
  text-align: center;
  align-items: flex-start;
  background-color: #bdae82;
  color: #000;
  border: none;
  cursor: pointer;
  transition: color 0.3s ease;
}
.button:hover {
  color: #fff;
}
@media (hover: none) {
  .button:hover {
    color: #000;
  }
  .button:active {
    color: #fff;
  }
}
.button:active {
  background-color: #b39d5d;
}
.button-inactive {
  background-color: transparent;
  border: 2px solid #bdae82;
  color: #bdae82;
}
.button-inactive:hover {
  color: #fff;
}
@media (hover: none) {
  .button-inactive:hover {
    color: #bdae82;
  }
  .button-inactive:active {
    color: #fff;
  }
}
.button-inactive:active {
  background-color: rgba(189, 174, 130, 0.2);
}

.overlay {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.5s ease, z-index 1s ease;
}
.overlay.open {
  z-index: 7;
  background-color: rgba(0, 0, 0, 0.5);
}

.header .header-container {
  display: flex;
  align-items: center;
  height: 90px;
}
@media (max-width: 768px) {
  .header .header-container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (max-width: 550px) {
  .header .header-container {
    padding: 0;
  }
}
.header .header-container .logo {
  z-index: 1;
}
.header .header-container .logo-icon {
  width: 50px;
  height: 50px;
  margin-top: 10px;
  fill: #fff;
  transition: fill 0.3s ease;
}
.header .header-container .logo-icon:hover {
  fill: #bdae82;
}
@media (hover: none) {
  .header .header-container .logo-icon:hover {
    fill: #fff;
  }
  .header .header-container .logo-icon:active {
    fill: #bdae82;
  }
}
.header .header-container .nav {
  position: static;
  z-index: 1;
  margin-left: auto;
}
@media (max-width: 900px) {
  .header .header-container .nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 20;
    display: block;
    width: calc(100vw - 150px);
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    transform: translateX(100vw);
    transition: transform 0.3s ease;
    display: none;
  }
}
@media (max-width: 900px) and (max-width: 619px) {
  .header .header-container .nav {
    width: 100vw;
  }
}
@media (max-width: 900px) {
  .header .header-container .nav .nav-list {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    margin-left: auto;
    padding: 134px 20px 20px 17%;
    background-color: #000;
    transition: transform 0.3s ease;
  }
  .header .header-container .nav .nav-list .nav-item {
    margin-bottom: 15px;
  }
  .header .header-container .nav .nav-list .nav-item .nav-link {
    font-size: 35px;
    line-height: 73px;
  }
}
@media (max-width: 900px) and (max-width: 619px) {
  .header .header-container .nav .nav-list {
    width: 100%;
    padding-left: 20px;
  }
}
@media (max-width: 900px) {
  .header .header-container .nav.open {
    transform: translateX(0);
  }
}
.header .header-container .nav .nav-list {
  display: flex;
}
.header .header-container .nav .nav-list .nav-item {
  margin-left: 30px;
}
.header .header-container .nav .nav-list .nav-item .nav-link {
  text-transform: capitalize;
}
.header .header-container .lang {
  z-index: 1;
  margin-left: 35px;
  font-weight: 700;
}
@media (max-width: 900px) {
  .header .header-container .lang {
    margin-left: auto;
  }
}
.header .header-container .lang .lang-input {
  display: none;
}
.header .header-container .lang .lang-input:checked + label {
  color: #bdae82;
}
.header .header-container .lang .lang-label {
  cursor: pointer;
  transition: color 0.3s ease-in;
}
.header .header-container .lang .lang-label:hover {
  color: #bdae82;
}
@media (max-width: 768px) {
  .header .header-container .lang .lang-label {
    margin: 0 5px;
  }
}
.header .header-container .lang .lang-divider {
  margin: 0 5px;
}
.header .header-container .theme-control {
  z-index: 1;
  margin-left: 35px;
  width: 45px;
  height: 45px;
  cursor: pointer;
}
@media (max-width: 550px) {
  .header .header-container .theme-control {
    margin-left: 10px;
  }
}
.header .header-container .theme-control svg {
  width: 45px;
  height: 45px;
}
.header .header-container .theme-control .theme-icon__light {
  fill: #fff;
  transition: fill 0.3s ease;
}
.header .header-container .theme-control .theme-icon__light:hover {
  fill: #bdae82;
}
@media (hover: none) {
  .header .header-container .theme-control .theme-icon__light:hover {
    fill: #fff;
  }
  .header .header-container .theme-control .theme-icon__light:active {
    fill: #bdae82;
  }
}
.header .header-container .theme-control .theme-icon__dark {
  display: none;
}
.header .header-container .burger {
  position: relative;
  z-index: 100;
  width: 50px;
  height: 50px;
  margin-left: 35px;
  display: none;
  cursor: pointer;
}
.header .header-container .burger:hover .burger-line {
  background-color: #bdae82;
}
@media (hover: none) {
  .header .header-container .burger:hover .burger-line {
    background-color: #fff;
  }
  .header .header-container .burger:active .burger-line {
    background-color: #bdae82;
  }
}
@media (max-width: 900px) {
  .header .header-container .burger {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
@media (max-width: 550px) {
  .header .header-container .burger {
    margin-left: 15px;
  }
}
.header .header-container .burger-line {
  position: absolute;
  left: 0;
  top: 24px;
  width: 50px;
  height: 3px;
  display: block;
  background-color: #fff;
  transition: transform 0.3s ease, background-color 0.3s ease;
}
.header .header-container .burger-line:nth-child(1) {
  transform: translate(0, -11px);
}
.header .header-container .burger-line:nth-child(2) {
  transform: translate(0, 11px);
}
.header .header-container .burger.open .burger-line:nth-child(1) {
  transform: rotate(45deg) translate(0, 0);
}
.header .header-container .burger.open .burger-line:nth-child(2) {
  transform: rotate(-45deg) translate(0, 0);
}
.header .header-container .burger.open .burger-line:nth-child(3) {
  transform: scale(0);
}

.main {
  margin-top: -90px;
}

.hero {
  display: flex;
  height: 910px;
  background: url("../assets/img/hero-bg.jpg") no-repeat center;
  background-size: cover;
}
@media (max-width: 768px) {
  .hero {
    height: 590px;
    background-position: -5px -30px;
    background-size: auto 620px;
  }
}
.hero .hero-container {
  width: 100%;
  height: 100%;
}
.hero .hero-container .hero-content {
  width: 530px;
  padding-top: 250px;
  padding-left: 60px;
}
.hero .hero-container .hero-content .hero-title {
  margin-bottom: 10px;
  font-size: 60px;
  line-height: 2em;
}
.hero .hero-container .hero-content .hero-info {
  margin-bottom: 25px;
}
@media (max-width: 768px) {
  .hero .hero-container .hero-content {
    width: 450px;
    padding-top: 168px;
    padding-left: 40px;
  }
  .hero .hero-container .hero-content .hero-title {
    margin-bottom: 20px;
    font-size: 32px;
  }
  .hero .hero-container .hero-content .hero-info {
    margin-bottom: 60px;
    line-height: 24.2px;
  }
}
@media (max-width: 550px) {
  .hero .hero-container .hero-content {
    width: 100%;
    padding: 168px 20px;
    text-align: center;
  }
}

.skills-items {
  display: flex;
  gap: 40px;
}
@media (max-width: 1100px) {
  .skills-items {
    flex-wrap: wrap;
    justify-content: center;
    gap: 36px 44px;
  }
}
@media (max-width: 768px) {
  .skills-items {
    gap: 25px 44px;
    margin-top: -5px;
    margin-bottom: 105px;
  }
}
@media (max-width: 550px) {
  .skills-items {
    margin-bottom: 0;
  }
}
.skills-items .skills-item {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
}
@media (max-width: 1100px) {
  .skills-items .skills-item {
    flex-basis: 300px;
    flex-grow: unset;
    padding: 0 10px 45px;
  }
}
@media (max-width: 550px) {
  .skills-items .skills-item {
    padding: 0 10px 10px;
  }
}
.skills-items .skills-item__icon {
  width: 62px;
  height: 50px;
  display: block;
  margin-bottom: 50px;
  fill: #bdae82;
}
@media (max-width: 768px) {
  .skills-items .skills-item__icon {
    margin-bottom: 30px;
  }
}
@media (max-width: 550px) {
  .skills-items .skills-item__icon {
    margin-bottom: 10px;
  }
}
.skills-items .skills-item__title {
  margin-bottom: 20px;
  font-weight: 700;
  text-align: center;
}
@media (max-width: 550px) {
  .skills-items .skills-item__title {
    margin-bottom: 10px;
  }
}
.skills-items .skills-item__text {
  margin-bottom: 8px;
  font-size: 18px;
  line-height: 36px;
  text-align: center;
}
@media (max-width: 768px) {
  .skills-items .skills-item__text {
    line-height: 22px;
  }
}
.skills-items .skills-item:nth-child(3) .skills-item__icon {
  width: 66px;
}
.skills-items .skills-item:nth-child(4) .skills-item__icon {
  width: 33px;
}

.portfolio-buttons {
  display: flex;
  gap: 5px;
  width: max-content;
  margin: 0 auto 60px;
}
@media (max-width: 1100px) {
  .portfolio-buttons {
    width: 500px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 550px) {
  .portfolio-buttons {
    width: 100%;
  }
}
.portfolio-images {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap: 25px;
}
@media (max-width: 1100px) {
  .portfolio-images {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .portfolio-images {
    grid-template-columns: repeat(1, 450px);
  }
}
@media (max-width: 550px) {
  .portfolio-images {
    grid-template-columns: repeat(1, 1fr);
  }
}
.portfolio-images .portfolio-img {
  width: 100%;
}

.video button.player__button {
  padding: 0;
  margin: 0;
  display: inline-block;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.video button.player__button svg {
  fill: #b3b3b3;
}
.video button.player__button:hover svg {
  fill: #bdae82;
}
.video .player {
  position: relative;
  overflow: hidden;
}
.video .player__video {
  display: block;
  width: 100%;
}
.video .player .button-play__main {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  padding: 0;
  border: none;
  background-color: transparent;
  fill: #fff;
  cursor: pointer;
  transition: fill 0.3s ease;
}
.video .player .button-play__main:hover {
  fill: #bdae82;
}
@media (hover: none) {
  .video .player .button-play__main:hover {
    fill: #fff;
  }
  .video .player .button-play__main:active {
    fill: #bdae82;
  }
}
.video .player .button-play__main svg {
  width: 130px;
  height: 130px;
}
@media (max-width: 768px) {
  .video .player {
    margin: 0 -20px 0 -20px;
  }
  .video .player .button-play__main {
    transform: translate(-50%, -50%) scale(0.5);
  }
}
.video .player__controls {
  position: absolute;
  bottom: 0;
  padding: 0 10px;
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.75);
  transform: translateY(70px);
  transition: transform 0.3s ease 2s;
}
@media (max-width: 500px) {
  .video .player__controls {
    padding: 0 5px;
    height: 50px;
  }
}
.video .player__controls .player__progress {
  background: linear-gradient(to right, #bdae82 0%, rgba(255, 255, 255, 0.3) 0%);
  width: 100%;
  height: 10px;
  margin: 5px 0 10px;
  border-radius: 10px;
  outline: none;
  -webkit-appearance: none;
}
@media (max-width: 500px) {
